.todo-select {
  margin: 1rem;
  display: flex;
  flex-direction: column;
}
select,
option {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  outline: none;
  height: 2rem;
  width: 40%;
  margin-top: 1rem;
  text-align: center;
  border: none;
  background-color: #6c4343;
  font-size: 1.3rem;
  color: #fddf97;
  font-family: inherit;
  cursor: pointer;
  border-radius: 3%;
}
.newListBtn {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  width: 50%;
  height: 2rem;
  background-color: #6c4343;
  outline: none;
  border: none;
  color: #fddf97;
  border-radius: 1rem;
  cursor: pointer;

  transition: ease-out 0.2s;
  font-size: 1.2rem;
  font-family: 'Architects Daughter', cursive;
}

.newListBtn:hover {
  background-color: #fddf97;
  transition: 0.2s ease-in;
  color: #6c4343;
}

@media screen and (max-width: 768px) {
  .newListBtn {
    width: 100%;
    font-family: Arial, Helvetica, sans-serif;
  }
  select,
  option {
    font-family: Arial, Helvetica, sans-serif;
    width: 100%;
  }
}
