.todo-input {
  margin: 1rem;
  display: flex;
  flex-direction: column;
  font-size: 2.2rem;
  color: #fddf97;
}

.todo-input > * {
  margin: 0.5rem;
}
::placeholder {
  color: #fddf97;
}
.input-field {
  outline: none;
  width: auto;
  height: 2.5rem;
  text-align: center;
  border: none;
  background-color: #6c4343;
  color: #fddf97;
  border-radius: 3%;
  font-size: 1.2rem;
  font-family: 'Architects Daughter', cursive;
}
.input-submit {
  position: relative;
  width: 50%;
  height: 2rem;
  background-color: #6c4343;
  outline: none;
  border: none;
  color: #fddf97;
  border-radius: 1rem;
  cursor: pointer;
  left: 49.5%;

  transform: translateX(-50%);
  transition: ease-out 0.2s;

  font-size: 1.2rem;
  font-family: 'Architects Daughter', cursive;
}

.input-submit:hover {
  background-color: #fddf97;
  transition: 0.2s ease-in;
  color: #6c4343;
}

@media (max-width: 768px) {
  input[type='submit'] {
    width: 100%;
    position: relative;
    left: 50%;
    transform: translateX(-52%);
    font-family: Arial, Helvetica, sans-serif;
  }
  input[type='text'] {
    font-family: Arial, Helvetica, sans-serif;
  }
}
