.clear-all-btn {
  border: 1px solid black;
  background-color: #364968;
  color: #fddf97;
  padding: 1rem;
  width: 10%;
  margin: 0.5rem auto;
  border-radius: 1rem;
  cursor: pointer;
  font-size: 1.2rem;
  font-family: 'Architects Daughter', cursive;
  margin-bottom: none;
}

.clear-all-btn:hover {
  background-color: #fddf97;
  transition: 0.2s ease-in;
  color: #6c4343;
}
.clear-btn-cont {
  border-bottom: none;
}
@media screen and (max-width: 768px) {
  .clear-all-btn {
    width: 100%;
    font-family: Arial, Helvetica, sans-serif;
  }
}
