.empty {
  position: relative;
  color: #fddf97;
  margin-top: 1rem;
  font-size: 1.3rem;
  background-color: #364968;
  max-width: 1168px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 1rem;
}

.empty-text {
  font-size: 1.6rem;
  text-align: center;
}

@media (max-width: 768px) {
  .empty h2 {
    width: 100%;
  }
}
