@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  box-sizing: border-box;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(216, 216, 216);
}

.App {
  display: flex;
  flex-direction: column;
  width: 70vw;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  color: #fddf97;
  position: relative;
  margin: 1rem;
  border-radius: 1.5rem;
}

.todo-select {
  margin: 1rem;
  display: flex;
  flex-direction: column;
}
select,
option {
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  outline: none;
  height: 2rem;
  width: 40%;
  margin-top: 1rem;
  text-align: center;
  border: none;
  background-color: #6c4343;
  font-size: 1.3rem;
  color: #fddf97;
  font-family: inherit;
  cursor: pointer;
  border-radius: 3%;
}
.newListBtn {
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  text-align: center;
  width: 50%;
  height: 2rem;
  background-color: #6c4343;
  outline: none;
  border: none;
  color: #fddf97;
  border-radius: 1rem;
  cursor: pointer;

  transition: ease-out 0.2s;
  font-size: 1.2rem;
  font-family: 'Architects Daughter', cursive;
}

.newListBtn:hover {
  background-color: #fddf97;
  transition: 0.2s ease-in;
  color: #6c4343;
}

@media screen and (max-width: 768px) {
  .newListBtn {
    width: 100%;
    font-family: Arial, Helvetica, sans-serif;
  }
  select,
  option {
    font-family: Arial, Helvetica, sans-serif;
    width: 100%;
  }
}

.todo-input {
  margin: 1rem;
  display: flex;
  flex-direction: column;
  font-size: 2.2rem;
  color: #fddf97;
}

.todo-input > * {
  margin: 0.5rem;
}
::-webkit-input-placeholder {
  color: #fddf97;
}
:-ms-input-placeholder {
  color: #fddf97;
}
::placeholder {
  color: #fddf97;
}
.input-field {
  outline: none;
  width: auto;
  height: 2.5rem;
  text-align: center;
  border: none;
  background-color: #6c4343;
  color: #fddf97;
  border-radius: 3%;
  font-size: 1.2rem;
  font-family: 'Architects Daughter', cursive;
}
.input-submit {
  position: relative;
  width: 50%;
  height: 2rem;
  background-color: #6c4343;
  outline: none;
  border: none;
  color: #fddf97;
  border-radius: 1rem;
  cursor: pointer;
  left: 49.5%;

  -webkit-transform: translateX(-50%);

          transform: translateX(-50%);
  transition: ease-out 0.2s;

  font-size: 1.2rem;
  font-family: 'Architects Daughter', cursive;
}

.input-submit:hover {
  background-color: #fddf97;
  transition: 0.2s ease-in;
  color: #6c4343;
}

@media (max-width: 768px) {
  input[type='submit'] {
    width: 100%;
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-52%);
            transform: translateX(-52%);
    font-family: Arial, Helvetica, sans-serif;
  }
  input[type='text'] {
    font-family: Arial, Helvetica, sans-serif;
  }
}

.wrapper {
  display: flex;
  position: relative;
  flex-direction: column;
  max-width: 1168px;
  align-content: center;
  justify-content: center;
  background-color: #364968;
  font-family: inherit;
  border-radius: 1rem;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.sort {
  margin-top: 1.5rem;
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  background-color: #364968;
  padding: 1.3rem;
  border-radius: 3%;
  color: #fddf97;
  width: 30%;
}

.sort label {
  font-size: 1.5rem;
}

#sort {
  margin-top: 0;
  width: 50%;
  position: relative;
  color: #fddf97;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

@media (max-width: 768px) {
  .sort {
    width: 100%;
    font-family: Arial, Helvetica, sans-serif;
  }
  #sort {
    font-family: Arial, Helvetica, sans-serif;
    width: 100%;
  }
}

@media (min-width: 769px) and (max-width: 1250px) {
  #sort {
    width: 100%;
  }
}

.TodoApp {
  text-align: center;
  font-family: 'Architects Daughter', cursive;
  background-color: rgb(216, 216, 216);
}

@media (max-width: 768px) {
  .App {
    font-family: Helvetica, sans-serif;
  }
}

.task-holder {
  max-width: 1168px;
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  background-color: rgba(27, 27, 27, 0);
  margin-top: 1.5rem;
  padding-bottom: 0.1rem;
  padding-top: 1rem;
}

.empty {
  position: relative;
  color: #fddf97;
  margin-top: 1rem;
  font-size: 1.3rem;
  background-color: #364968;
  max-width: 1168px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  border-radius: 1rem;
}

.empty-text {
  font-size: 1.6rem;
  text-align: center;
}

@media (max-width: 768px) {
  .empty h2 {
    width: 100%;
  }
}

.list {
  position: relative;
  margin-left: 1rem;
  margin-right: 1rem;
  margin-bottom: 1rem;
  background-color: #364968;
  border-radius: 1rem;
  color: #fddf97;
}

.title {
  position: relative;
  font-size: 1.5rem;
  padding-bottom: 1rem;
  color: #fddf97;
}

.deleteList {
  font-size: 1.3rem;
  color: #fddf97;
  position: relative;
  right: -48%;
  top: 0.7rem;
}

.deleteList:hover {
  color: rgb(122, 29, 29);
  cursor: pointer;
}

@media (max-width: 768px) {
  .deleteList {
    position: relative;
    right: -45%;
  }
}

.deleteItem i {
  position: relative;
  width: 1rem;
  height: 1rem;
  color: #fddf97;
}
.deleteItem {
  padding-right: 0.6rem;
}
.deleteItem:hover {
  color: #6c4343;
  cursor: pointer;
}
.items-list {
  padding-bottom: 1rem;
  width: auto;
  border-radius: 3%;
}
.single-item {
  font-size: 1.4rem;
  position: relative;
  max-width: 1168px;
  width: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  list-style: none;
  border-bottom: 1px solid #fddf97;
  left: 50%;
  -webkit-transform: translateX(-51.8%);
          transform: translateX(-51.8%);
  margin-bottom: 1rem;
  word-break: break-all;
  padding-bottom: 0.3rem;
}
span {
  padding-left: 0.5rem;
}
@media (max-width: 768px) {
  li {
    width: 100%;
    left: 50%;
    -webkit-transform: translateX(-54.5%);
            transform: translateX(-54.5%);
  }
}

.clear-all-btn {
  border: 1px solid black;
  background-color: #364968;
  color: #fddf97;
  padding: 1rem;
  width: 10%;
  margin: 0.5rem auto;
  border-radius: 1rem;
  cursor: pointer;
  font-size: 1.2rem;
  font-family: 'Architects Daughter', cursive;
  margin-bottom: none;
}

.clear-all-btn:hover {
  background-color: #fddf97;
  transition: 0.2s ease-in;
  color: #6c4343;
}
.clear-btn-cont {
  border-bottom: none;
}
@media screen and (max-width: 768px) {
  .clear-all-btn {
    width: 100%;
    font-family: Arial, Helvetica, sans-serif;
  }
}

#input {
  position: relative;
  max-width: 1168px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  background-color: #364968;
  border-radius: 3rem;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
form {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 2rem;
}

label {
  margin-top: 1rem;
  margin-bottom: 1.3rem;
}
.text-field {
  width: 25%;
  text-align: center;
  border: 1px solid #fddf97;
  outline: none;
  border-radius: 0.3rem;
  background-color: #6c4343;
  color: #fddf97;
  height: 1.7rem;
  font-size: 1.1rem;
  font-family: inherit;
}

.text-field::-webkit-input-placeholder {
  color: #fddf97;
}

.text-field:-ms-input-placeholder {
  color: #fddf97;
}

.text-field::placeholder {
  color: #fddf97;
}

.submit-input {
  font-size: 1rem;
  width: 10rem;
  height: 2rem;
  cursor: pointer;
  background-color: #6c4343;
  outline: none;
  border: none;
  border-radius: 0.3rem;
  color: #fddf97;
  font-family: inherit;
  letter-spacing: 0.13rem;
  border: 1px solid #fddf97;
  transition: 0.2s ease-out;
  margin-bottom: 1.3rem;
  margin-top: 1.3rem;
}

.submit-input:hover {
  background-color: #fddf97;
  transition: 0.2s ease-in;
  color: #6c4343;
}

.results {
  position: absolute;
  width: 24%;
  background-color: #6c4343;
  border-radius: 0.3rem;

  margin: 0;
  top: 60%;

  border: 1px solid #fddf97;
  padding: 0.5rem;
}
.results .result-list {
  text-align: left;
  list-style: none;
  padding: 0;
  margin: 0;
}
.results div {
  font-size: 1rem;
  border-bottom: 1px solid #fddf97;
  width: 100%;
  padding: 0.3rem 0 0.3rem 0;
  cursor: pointer;
}

.results div:hover {
  background-color: #364968;
}

.homepage nav {
  margin-bottom: 2rem;
  background-color: #364968;
  border-radius: 0.3rem;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}
nav h2 span {
  color: rgb(162, 212, 82);
}
.home-list {
  list-style: none;
  display: flex;
}

.home-list > li {
  margin-right: 2rem;
  font-size: 1.3rem;
}
a {
  text-decoration: none;
  color: #fddf97;
  font-weight: bold;
}

.home-list a:hover {
  color: rgb(162, 212, 82);
}

.holder {
  display: flex;
  flex-direction: column;
  border-top: 1px #fddf97 solid;
  background-color: #364968;
  padding-bottom: 1rem;
  border-radius: 3rem;

  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
h4 {
  letter-spacing: 0.22rem;
  font-size: 1.4rem;
}
.information-box {
  display: flex;
  justify-content: center;
  align-items: center;
}
.information-box > * {
  margin: 1rem;
  border: 1px #fddf97 solid;
  padding: 1rem;
  border-radius: 1.5rem;
  background-color: #6c4343;
}

.country-display {
  font-size: 2.5rem;
  margin: 0.6rem;
}

.information-box > h4 {
  letter-spacing: 15rem;
}

.home-body {
  max-width: 1168px;
  background-color: #364968;
  border-radius: 0.3rem;
  padding: 1rem;
}

h2 span {
  color: rgb(162, 212, 82);
}

h4 {
  text-align: center;
}

